@import url('http://fonts.cdnfonts.com/css/norwester');
@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');

/* Resume colours: #e14631 #313638 */

.item {
  position: relative;
  display: flex;
  flex-flow: column nowrap;
  font-family: 'Lato';
  text-align: justify;
  padding: 1rem;
  padding-bottom: 3rem;
  margin: 1rem;
  width: 20rem;
  /* border: 2px solid #e14631; */
  color: white;
  background-image: linear-gradient(0.125turn, #1b1d1f, #313638);
  background-size: 100vw 100vh;
  background-attachment: fixed;
}

.item-title {
  font-family: 'Norwester';
  position: relative;
  left: -1rem;
  top: -2rem;
  padding: 0.25rem 0.5rem;
  /* border-radius: 10px 0 0 0; */
  /* border: 2px solid white; */
  color: white;
  background-color: #e14631;
  /* background-image: linear-gradient(0.125turn, #1b1d1f, #313638); */
  /* background-size: 100vw 100vh; */
  /* background-attachment: fixed; */
  font-size: 1.5rem;
}

.item-links {
  position: absolute;
  bottom: -1rem;
  right: 0;
  display: inline-flex;
  justify-content: flex-end;
  align-items: center;
  transition: all 0.2s;
}

.item-links a {
  background-image: linear-gradient(0.125turn, #1b1d1f, #313638);
  background-size: 100vw 100vh;
  background-attachment: fixed;
  padding: 0.5rem 1rem;
  /* margin-left: 0.5rem; */
  border: 1px solid white;
  font-family: 'Norwester';
  font-size: 1rem;
  font-weight:lighter;
  color: white;
  text-decoration: none;
  /* text-shadow: 0 0 0 white, 0 0 0 #e14631; */
  transition: all 0.2s;
}

.item-links a:hover {
  color: #e14631;
  border: 1px solid #e14631;
  /* text-shadow: -1px -1px 0 white, 1px 1px 0 #e14631; */
}

.item-media {
  width: 100%;
  filter: grayscale(1);
  transition: all 0.2s;
  margin-bottom: 1rem;
}

.item-media:hover {
  filter: grayscale(0);
}
